import StringCrypto from 'string-crypto'

const ANSWER = 'e4af6a763601a6e475a5c26ec1b5e82b:';
const ENCRYPTED_CIPHER = `c02ed2d59a24790e3761c3f5a3b6719d:`

const options = {
  salt: '2f0ijf2039j23r09j2fg45o9ng98um4o',
  iterations: 10,
  digest: 'sha512',
};

const { decryptString} = new StringCrypto(options);

const tryPassword = (password) => {
    if( decryptString(ENCRYPTED_CIPHER, password) === ANSWER){
        console.log('correct password!')
        return true
      }else{
          console.log('wrong password') 
          return false
      }
}

export {
    tryPassword
}